<template>
	<v-app v-scroll="onScroll">
		<v-navigation-drawer
			class="hidden-md-and-up"
			v-model="drawer"
			app
			clipped
			disable-resize-watcher
			elevate-on-scroll
			color="">
			<v-list dense>
				<v-list-item link v-for="item in items" :key="item.title"
					@click="$vuetify.goTo(item.link, options)">
					<v-list-item-action>
						<v-icon>{{ item.icon }}</v-icon>
					</v-list-item-action>
					<v-list-item-content>
						<v-list-item-title style="text-transform: uppercase;">{{ item.title }}</v-list-item-title>
					</v-list-item-content>
				</v-list-item>
			</v-list>
		</v-navigation-drawer>
		<v-app-bar
			fixed
			:color="appColor"
			elevation="0"
			dark
			scroll-threshold="100">

			<!-- <v-app-bar-nav-icon></v-app-bar-nav-icon> -->
			<v-app-bar-nav-icon @click.stop="drawer = !drawer" class="hidden-md-and-up" :color="appFontColor"></v-app-bar-nav-icon>
			<v-toolbar-title class="" 
				style="font-weight: bold;letter-spacing: 1px;font-family: 'Exo 2', sans-serif;transition: .5s;"
				:style="{ color: appFontColor, 'font-size': appFontSize }">
				<!-- <v-icon style="font-size: 1.1em;">mdi-rocket</v-icon> -->
				JPROJECT
			</v-toolbar-title>

			<v-spacer></v-spacer>
			<v-toolbar-items class="hidden-sm-and-down">
				<v-btn v-for="item in items" :key="item.title" color="transparent" depressed 
					@click="$vuetify.goTo(item.link, options)">
					<b :style="{ color: appFontColor }">{{ item.title }}</b>
				</v-btn>
			</v-toolbar-items>
		</v-app-bar>

		<v-main>
			<HeroSection/>
			<ServiceSection/>
			<PortofolioSection/>
			<AboutSection/>
			<ContactSection/>
		</v-main>
		<FooterSection/>
	</v-app>
</template>

<script>
// import HelloWorld from './components/HelloWorld';
import HeroSection from './components/HeroSection';
import ServiceSection from './components/ServiceSection';
import PortofolioSection from './components/PortofolioSection';
import AboutSection from './components/AboutSection';
import ContactSection from './components/ContactSection';
import FooterSection from './components/FooterSection';

export default {
	name: 'App',

	components: {
		HeroSection, ServiceSection, PortofolioSection, AboutSection, ContactSection, FooterSection
	},

	created () {
		// window.addEventListener('scroll', this.handleScroll);
	},

	computed: {
		options () {
			return {
				duration: this.duration,
				offset: this.offset,
				easing: this.easing,
			}
		},
	},

	destroyed () {
		// window.removeEventListener('scroll', this.handleScroll);
	},

	watch: {
		offsetTop(newOffset) {
			this.appColor = newOffset ? '#272727' : 'transparent'
			this.appFontColor = newOffset ? 'white' : '#222222'
			this.appFontSize = newOffset ? '2em' : '3em'
		}
	},

	data: () => ({
		offsetTop: 0,
		appColor: 'transparent',
		appFontColor: '#222222',
		appFontSize: '3em',
		drawer: false,
		items: [
			{ title: 'Home', icon: 'mdi-home', link: '#section-home', color:'#febd01'},
			{ title: 'Service', icon: 'mdi-play', link: '#section-service', color:'#febd01'},
			{ title: 'Portofolio', icon: 'mdi-gamepad-square', link: '#section-portofolio', color:'#febd01'},
			{ title: 'About', icon: 'mdi-alarm-bell', link: '#section-about', color:'#febd01'},
			{ title: 'Contact', icon: 'mdi-rocket-launch', link: '#section-contact', color:'#febd01'},
		],
		duration: 1000,
		offset: 50,
		easing: 'easeInOutCubic',
	}),

	methods: {
		// handleScroll (event) {
			// Any code to be executed when the window is scrolled
		// },
		onScroll (e) {
			if (typeof window === 'undefined') return
			const top = window.pageYOffset ||   e.target.scrollTop || 0

			this.offsetTop = top > 100
		},
	}
};
</script>
